.contacts__section{
    background-color: var(--main-theme-color);
    margin-top: 345px;
    padding-bottom: 20px;

    @media screen and (min-width:768px){
        margin-top: 300px;
        padding-bottom: 20px;
    }

    @media screen and (min-width:1200px){
        margin-top: 100px;
        padding-bottom: 60px;
    }
}

.contacts{
    justify-content: center;
    @include grid(1,20px);



    @media screen and (min-width:768px){
        @include grid(3,16px);
    }

    @media screen and (min-width:1200px){
        @include grid(3,30px);
    }
    
}
.contacts__item{
    width: 280px;
    padding-left: 32px;
    padding-top: 40px;
    padding-right: 32px;
    padding-bottom: 40px;
    background: var(--secondary-color);
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px; 

    @media screen and (max-width:767px){

    &:first-child{
        margin-top: -345px;
    }
    }


    @media screen and (min-width:768px){
        width: 220px;
        margin-top: -300px;
    }
    @media screen and (min-width:1200px){
        width: 368px;
        margin-top: -100px;
    }
   
}

.contacts__caption{
    @extend %text-style;

   
    height: 16px;
    background: var(--accent-color);
    border-radius: 4px;
    
    padding-left: 20px;
    padding-right: 20px;
    

}

.contacts__subtitle{
    @extend %text-style;
    color: var(--primary-color);


    display: block;
    margin-top: 20px;
}

.contacts__address{
    @extend %text-style;
    color: var(--palette-color-3);

    margin-top: 8px;
    margin-bottom: 24px;
    
   

}

.contacts__desc{
    @extend %text-style;
    color: var(--primary-color);

    display: block;
}

.contacts__list-schedule{
    padding-top: 24px;
    padding-bottom: 24px;
    border-top: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
}

.contacts__text{
    display: block;
    color: var(--pale-color);
}

.contacts__data{
    @extend %text-style;
    line-height: 18px;
    color: var(--primary-color);

    margin-top: 24px;

}

.contacts__mail{
    display: block;
    color: var(--primary-color);
}

.contacts__tel{
    display: block;
    color: var(--primary-color);


}

.contacts__buttons-list{
    margin-top: 32px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
}

.button__contacts-button{
    min-width: 196px;
}

